<template>
  <div id="InvoiceFileGeneration">
    <div class="topOpt">
      <el-form :inline="true" :model="formTop">
        <el-form-item label="订单号">
          <el-input
            v-model="formTop.order"
            placeholder="请输入订单号"
          ></el-input>
        </el-form-item>
        <el-form-item label="国家">
          <el-select v-model="formTop.country" placeholder="请选择国家">
            <el-option value="GB" label="英国"></el-option>
            <el-option value="other" label="其他"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="searchInfo">搜索</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="editSubmit">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="dataList" v-if="showDetail">
      <p>
        <span
          >姓名：<i>{{ name }}</i>
        </span>
        <span
          >城市：<i>{{ city }}</i>
        </span>
        <span
          >地址：<i>{{ address }}</i>
        </span>
        <span
          >邮编：<i>{{ zip_code }}</i>
        </span>
        <span
          >日期：<i>{{ dates }}</i>
        </span>
        <span
          >订单号:<i>{{ order_id }}</i></span
        >
        <span
          >发票单号:<i>{{ Invoice_number }}</i></span
        >
      </p>
    </div>
    <div class="tableCon">
      <el-table border :data="tableData" stripe style="width: 100%">
        <el-table-column align="center" prop="0" label="QTY" width="180">
        </el-table-column>
        <el-table-column align="center" prop="1" label="sku" width="180">
        </el-table-column>
        <el-table-column
          align="center"
          prop="2"
          label="Item Description"
          width="180"
        >
          <template slot-scope="scope">
            <el-select v-model="scope.row[2]">
              <el-option
                v-for="(item, index) in descriptArr"
                :key="index"
                :value="item"
                :label="item"
              ></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="3" label="Unit Price(excl. VAT)">
        </el-table-column>
        <el-table-column align="center" prop="4" label="VAT Rate">
        </el-table-column>
        <el-table-column align="center" prop="5" label="Unit Price(incl. VAT)">
          <template slot-scope="scope">
            <span
              style="position: absolute;
    top: 50%;
    left: 35px;
    transform: translateY(-50%);"
              :style="{ zIndex: len && scope.$index === len ? -1 : 1 }"
              >{{ moneyFlag }}</span
            >
            <el-input
              style="width:220px"
              placeholder="请输入数字"
              @change="
                val => {
                  handleChange(val, scope.$index);
                }
              "
              v-model="scope.row[5]"
            >
            </el-input>

            <div
              class="mask"
              :style="{ zIndex: len && scope.$index === len ? 1 : -1 }"
            ></div>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="6" label="Total Price(incl. VAT)">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    len() {
      return this.tableData.length - 1;
    }
  },
  data() {
    return {
      editIdxArr: [],
      showDetail: false,
      priceRate: {
        GB: {
          label: "20%",
          value: 0.2
        },
        other: {
          label: "18%",
          value: 0.18
        }
      },
      descriptArr: [
        "JOISCOPE Desk",
        "HOMEYFINE DESK",
        "JOISCOPE SHOEBOX",
        "HOMEYFINE SHOEBOX",
        "JOISCOPE WARDROBE",
        "HOMEYFIN WARDROBE"
      ],
      formTop: {
        order: "",
        country: ""
      },
      tableData: [],
      name: "",
      address: "",
      city: "",
      zip_code: "",
      Invoice_number: "",
      dates: "",
      order_id: "",
      total: 0
    };
  },

  methods: {
    editSubmit() {
      if (this.editIdxArr.length === 0) return;
      // 计算总计
      let dataArr = [];
      this.editIdxArr.forEach((i, idx) => {
        dataArr.push(this.tableData[i]);
        console.log(idx, dataArr[idx]);
        dataArr[idx]["5"] = this.moneyFlag + dataArr[idx]["5"];
      });

      let fd = new FormData();
      fd.append("data", JSON.stringify(dataArr));
      fd.append("all_price", this.total);
      fd.append("name", this.name);
      fd.append("city", this.city);
      fd.append("address", this.address);
      fd.append("zip_code", this.zip_code);
      fd.append("dates", this.dates);
      fd.append("order_id", this.formTop.order);
      fd.append("Invoice_number", this.Invoice_number);
      let url = this._url2 + "server/order_management/create_invoice_data/";
      this.$axios.post(url, fd).then(res => {
        if (res.data.code !== 200) {
          this.$message.warning("请求错误");
          return;
        }
        let url = res.data.data;
        if (url) {
          url = this.formatUrl(url);
          let a = document.createElement("a");
          a.setAttribute("href", this._url2 + url);
          a.setAttribute("download", "");
          document.body.appendChild(a);
          a.click();
        }
      });
    },
    formatUrl(url) {
      return url.replace("/home/by_operate/", "");
    },
    handleChange(val, index) {
      if (isNaN(val)) {
        this.$set(this.tableData[index], "5", "");
        return;
      }

      if (!this.editIdxArr.includes(index)) {
        this.editIdxArr.push(index);
      }

      let unitP = (Number(val) * (1 - this.rate_num)).toFixed(2);
      unitP = this.moneyFlag + unitP;
      this.$set(this.tableData[index], 3, unitP);
      // this.tableData[index]["3"] = unitP;
      this.$set(
        this.tableData[index],
        6,
        this.moneyFlag + Number(this.tableData[index][0]) * Number(val)
      );

      // 改变总计

      let total = 0;
      this.tableData.forEach((i, idx) => {
        console.log(i);
        if (idx !== this.tableData.length - 1) {
          total += Number(i[6].slice(1));
        }
      });
      total = this.moneyFlag + total;
      console.log("总计", total);
      if (this.tableData.length > 0) {
        let len = this.tableData.length - 1;
        this.$set(this.tableData[len], "6", total);
        this.total = total;
      }
      console.log(this.tableData[index]);
    }, // 改变税前价格
    searchInfo() {
      if (this.formTop.order === "" || this.formTop.station === "") {
        this.$message.warning("请输入订单号并选择国家进行查询！");
        return;
      }
      let url = this._url2 + "server/order_management/count_invoice_data/";
      this.$axios
        .get(url, {
          params: {
            order_id: this.formTop.order,
            country: this.formTop.country
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.name = res.data.name;
            this.address = res.data.address;
            this.city = res.data.city;
            this.zip_code = res.data.zip_code;
            this.Invoice_number = res.data.Invoice_number;
            this.dates = res.data.dates;
            this.order_id = res.data.order_id;
            // 展示数据
            this.showDetail = true;
            let allData = res.data.data; // 数据
            let rate = this.formTop.country === "GB" ? "20%" : "18%";
            this.moneyFlag = this.formTop.country === "GB" ? "£" : "€";
            this.rate_num = rate === "20%" ? 0.2 : 0.18;
            this.tableData = [];
            for (let key in allData) {
              let rowObj = [];
              let item = allData[key];
              for (let i in item) {
                if (i === "0") {
                  rowObj[1] = item[i];
                }
                if (i === "1") {
                  rowObj["0"] = item[i];
                }
              }
              rowObj["2"] = this.descriptArr[0];
              rowObj["3"] = "0";
              rowObj["4"] = rate;
              rowObj["5"] = "";
              rowObj["6"] = "0";
              this.tableData.push(rowObj);
            }
            let zj = ["", "", "", "", "", "TOTAL", 0];
            this.tableData.push(zj);
          } else {
            this.$message.warning(res.data.msg);
          }
        });
    }
  }
};
</script>

<style scoped>
#InvoiceFileGeneration .topOpt {
  padding-top: 22px;
  padding-left: 22px;
  background: rgba(255, 255, 255, 0.9);
}
#InvoiceFileGeneration .mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.dataList > p {
  padding: 10px 20px;
  color: #fff;
}
.dataList > p > span {
  margin: 0 10px;
}
.dataList > p i {
  margin-left: 4px;
}
</style>
<style>
.tableCon .el-table__row:last-child .el-select {
  opacity: 0;
  z-index: -1;
}
.tableCon .el-table__row:last-child .el-input .el-input__inner {
  border: none;
  background: transparent;
}
</style>



